/* ==========================================================================
   Searchform
   ========================================================================== */

@use "../../mixin/mq" as mq;

.Search {
  $_block: '.Search';
  margin: 40px 0 0;

  .SearchForm {
    position: relative;
  }

  .SearchForm__input {
    width: 94%;
    font-size: 2rem;
    padding: 10px 0 10px 10px;
    border-radius: 10px;
  }

  .SearchForm__submit {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
    width: 50px;
    height: 100%;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
    background-color: #2C3036;

    > i {
      color: #fff;
      font-size: 20px;
    }
  }
}


/* Responsive (768pxから960pxまでのレイアウト調整)
========================================================================== */
@media screen and (max-width: 960px) {
  .Search {
    $_block: '.Search';
    margin: 0;
  }
}
